.internal-wiki {
	@include r(767) {
		background-color: $white; }

	&__container {}

	&__title-inner {
		position: fixed;
		width: 100%;
		top: 78px;
		padding: 15px 0 0;
		background-color: $blue; }

	&__title {}

	&__inner {
		display: flex;
		justify-content: space-between;
		margin: 248px 0 40px;
		@include r(767) {
			flex-direction: column-reverse;
			padding-top: 30px; } }

	&__text {
		width: calc(60% - 15px);
		p {
			@include markpro;
			font-size: 18px;
			line-height: 1.53;
			color: $gray;
			padding-bottom: 8px; }
		@include r(767) {
			width: 100%;
			p {
				font-size: 16px; } } }

	&__sidebar {
		width: calc(40% - 15px);
		@include r(767) {
			width: 100%; } }

	&__image {
		width: 100%;
		height: 500px;
		object-fit: cover;
		@include r(767) {
			max-height: 360px; } }

	&__badges {
		margin-top: 20px; }

	&__badge {
		margin-left: 0;
		margin-right: 5px; }

	&__projects {
		background-color: $white;
		padding: 40px;
		margin-top: 20px;
		@include r(767) {
			padding: 20px 0; } }

	&__project-title {
		@include markpro;
		font-size: 21px;
		color: #484848;
		text-transform: uppercase;
		margin-bottom: 8px; }

	&__list {}

	&__item {}

	&__link {
		@include markpro;
		font-size: 21px;
		line-height: 1.53;
		color: $blue;
		&:hover {
			color: rgba($blue, .7); } } }
